header{
    min-height: 135vh;
    /* max-height: 120vh; */
    overflow-x: hidden;
    overflow-y: hidden;
    padding-top: 3rem;
    /* position:relative;   */
}

.header__container{
    position: relative  ;  
    /* height: 120vh; */
}

header h5{
    font-size: 2em;
}
/* === CTA === */
.cta{
    margin-top: 2rem;
    display: flex;
    gap: 1.3rem; 
}

/* === HEADER SOCIALS=== */
.header__socials{
    display:flex;
    flex-direction: row;
    align-items: center;
    gap: 1.5rem;
    position: relative;
    left: 0;
    top:2.5rem;
}

.header__socials::before{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}


.scroll__down{
    scroll-behavior: smooth;
    position: absolute;
    right: -6rem;
    transform: rotate(90deg);
    font-size: 0.9rem;
}

.chat-me-scroll{
    opacity: .6;
    position: fixed;
    bottom: 15px;
    right: 28px;
    width: 70px;
    height: 70px;
    border-radius: 10%;
    background: var(--color-bg-variant);
    transition: var(--transition);
    color: transparent; 
    cursor: pointer;
}

.chat-me-scroll:hover{    
    padding: 17px 30px;
    width: 168px;
    color: var(--color-primary);
}

.cms-text{
    transition-delay: 2ms;
}

/* Responsive for medium devices */
@media (min-width: 750px) and (max-width: 1024px) {
    header{
        min-height: 120vh;
    }
    
    .chat-me-scroll{
        display: none;
    }
  
    .scroll__down{
        bottom: 15rem;
        margin-right: 3rem;
    }
}

@media screen and (max-width: 600px){
   header{
        min-height:145vh;          
    }
    .chat-me-scroll{
        display: none;
    }
      .scroll__down{
        /* bottom: 50rem; */
        /* margin-right: 15rem; */
    }
}

/* Responsive for small devices */
@media (min-width: 200px) and (max-width: 450px){
    header{
        min-height:130vh;          
    }
    
    .header__socials, 
    .scroll__down{
        display: none;          
    }


    .chat-me-scroll{
        display: none;
    }

    .cta a{
        font-size: smaller;
    }

}
